module.exports = "class NavJustified extends React.Component {\n  handleSelect(selectedKey) {\n    alert(`selected ${selectedKey}`);\n  }\n\n  render() {\n    return (\n      <div>\n        <Nav\n          bsStyle=\"tabs\"\n          justified\n          activeKey={1}\n          onSelect={key => this.handleSelect(key)}\n        >\n          <NavItem eventKey={1} href=\"/home\">\n            NavItem 1 content\n          </NavItem>\n          <NavItem eventKey={2} title=\"Item\">\n            NavItem 2 content\n          </NavItem>\n          <NavItem eventKey={3} disabled>\n            NavItem 3 content\n          </NavItem>\n        </Nav>\n        <br />\n        <Nav\n          bsStyle=\"pills\"\n          justified\n          activeKey={1}\n          onSelect={key => this.handleSelect(key)}\n        >\n          <NavItem eventKey={1} href=\"/home\">\n            NavItem 1 content\n          </NavItem>\n          <NavItem eventKey={2} title=\"Item\">\n            NavItem 2 content\n          </NavItem>\n          <NavItem eventKey={3} disabled>\n            NavItem 3 content\n          </NavItem>\n        </Nav>\n      </div>\n    );\n  }\n}\n\nrender(<NavJustified />);\n"


